<template>
  <div class="search">
    <van-nav-bar fixed title="商品列表" left-arrow @click-left="$router.go(-1)" />

    <van-search
      readonly
      shape="round"
      background="#ffffff"
      :value="getname||'请输入内容'"
      show-action
      @click="$router.push('/search')"
    >
      <template #action>
        <van-icon class="tool" name="apps-o" />
      </template>
    </van-search>

    <!-- 排序选项按钮 -->
    <div class="sort-btns">
      <div class="sort-item">综合</div>
      <div class="sort-item">销量</div>
      <div class="sort-item">价格</div>
    </div>

    <div class="goods-list">
      <GoodsItem v-for="item in proList" :key="item.goods_id" :it="item"></GoodsItem>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import GoodsItem from "@/components/GoodsItem.vue";
import { getProList } from "@/api/product";
export default {
  name: "SearchIndex",
  data() {
    return {
      page: 1,
      proList: []
    };
  },
  components: {
    GoodsItem
  },
  computed: {
    getname() {
      return this.$route.query.search;
    }
  },
  async created() {
    const {
      data: { list }
    } = await getProList({
      categoryId: this.$route.query.categoryId,
      goodsName: this.getname,
      page: this.page
    });
    console.log(list);
    this.proList = list.data;
  }
};
</script>

<style lang="less" scoped>
.search {
  padding-top: 46px;
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .tool {
    font-size: 24px;
    height: 40px;
    line-height: 40px;
  }

  .sort-btns {
    display: flex;
    height: 36px;
    line-height: 36px;
    .sort-item {
      text-align: center;
      flex: 1;
      font-size: 16px;
    }
  }
}
.goods-list {
  background-color: #f6f6f6;
}
</style>
